import React from 'react';
import PropTypes from 'prop-types';
import * as styles from './style.module.scss';

export default function ItemInfo({ name, rarity, attack, defence }) {
  return (
    <div className={styles.infoContainer}>
      <div>
        <div className={styles.title}>{name}</div>
        <div className={styles.traitPercent}>{rarity}% HAVE THIS TRAIT</div>
      </div>
      <div className={styles.divider} />
      <div>
        <div className={styles.traitValue}>+{attack}</div>
        <div className={styles.traitText}>ATTACK</div>
      </div>
      <div className={styles.divider} />
      <div>
        <div className={styles.traitValue}>+{defence}</div>
        <div className={styles.traitText}>DEFENCE</div>
      </div>
    </div>
  );
}

ItemInfo.propTypes = {
  name: PropTypes.string,
  rarity: PropTypes.number,
  attack: PropTypes.number,
  defence: PropTypes.number,
};
