import React from 'react';
import PropTypes from 'prop-types';
import * as styles from './style.module.scss';
import ItemInfo from '../ItemInfo';

export function CharacterBoard({
  prevCharacter,
  character,
  nextCharacter,
  traitIndex,
  onClick,
}) {
  return (
    <div className={styles.characContainer}>
      <div className={styles.traitBoard}>
        <img
          src="image/Left Arrow.png"
          onClick={() => onClick(traitIndex - 1)}
          className={styles.leftArrowImg}
        />
        {prevCharacter && (
          <div className={styles.prev}>
            <div className={styles.characBoard}>
              <img src={`image/${prevCharacter.type} Background.png`} />
              <img src={prevCharacter.imageUrl} className={styles.character} />
            </div>
          </div>
        )}
        <div className={styles.characBoard}>
          <img src={`image/${character.type} Background.png`} />
          <img src={character.imageUrl} className={styles.character} />
        </div>
        {nextCharacter && (
          <div className={styles.next}>
            <div className={styles.characBoard}>
              <img src={`image/${nextCharacter.type} Background.png`} />
              <img src={nextCharacter.imageUrl} className={styles.character} />
            </div>
          </div>
        )}
        <img
          src="image/Right Arrow.png"
          onClick={() => onClick(traitIndex + 1)}
          className={styles.rightArrowImg}
        />
      </div>
      <ItemInfo
        name={character.name}
        rarity={character.rarity}
        attack={character.attack}
        defence={character.defence}
      />
    </div>
  );
}

CharacterBoard.propTypes = {
  prevCharacter: PropTypes.object,
  character: PropTypes.object,
  nextCharacter: PropTypes.object,
  name: PropTypes.string,
  rarity: PropTypes.number,
  attack: PropTypes.number,
  defence: PropTypes.number,
  imageUrl: PropTypes.string,
  traitIndex: PropTypes.number,
  onClick: PropTypes.func,
};
