import React from 'react';
import PropTypes from 'prop-types';
import * as styles from './style.module.scss';

export function DecorButton({ title, onClick, active }) {
  return (
    <button
      className={[
        styles.decorButton,
        title == 'FIRE'
          ? styles.fire
          : title == 'WATER'
          ? styles.water
          : title == 'FOREST'
          ? styles.forest
          : '',
        active ? styles.active : '',
      ].join(' ')}
      onClick={onClick}
    >
      <img className={styles.leftDecor} src="image/Button Decor.png" />
      {title}
      <img className={styles.rightDecor} src="image/Button Decor.png" />
      {title == 'FIRE' ? (
        <img className={styles.typeDecor} src="image/Fire.png" width="50px" />
      ) : (
        ''
      )}
      {title == 'WATER' ? (
        <img className={styles.typeDecor} src="image/Water.png" width="70px" />
      ) : (
        ''
      )}
      {title == 'FOREST' ? (
        <img className={styles.typeDecor} src="image/Forest.png" width="70px" />
      ) : (
        ''
      )}
    </button>
  );
}

DecorButton.propTypes = {
  title: PropTypes.string,
  onClick: PropTypes.func,
  active: PropTypes.bool,
};
