import React, { useEffect, useState } from 'react';
import TraitList from '../../components/TraitList';
import { DecorButton } from '../../components/Buttons';
import * as styles from './style.module.scss';
import SelectBox from '../../components/SelectBox';
import { CharacterBoard } from '../../components/CharacterBoard';

import { rarities, mocktraits } from '../../mockdata';

export default function Home() {
  const [selectedRarity, setSelectedRarity] = useState(0);
  const [selectedTrait, setSelectedTrait] = useState(0);
  const selectTrait = prop => {
    prop >= 0 && prop < traits.length && setSelectedTrait(prop);
  };
  const [selectedAttribute, setSelectedAttribute] = useState(0);
  const [traits, setTraits] = useState(mocktraits);

  useEffect(() => {
    let t;
    let temp = mocktraits;
    switch (selectedAttribute) {
      case 2:
        t = 'Fire';
        break;
      case 3:
        t = 'Water';
        break;
      case 4:
        t = 'Forest';
        break;
    }
    if (selectedAttribute > 1) {
      temp = mocktraits.filter(trait => trait.type == t);
    }
    setTraits(temp);
    setSelectedTrait(0);
  }, [selectedAttribute]);

  useEffect(() => {
    let t;
    let temp = mocktraits;
    switch (selectedRarity) {
      case 1:
        t = 0.01;
        break;
      case 2:
        t = 0.02;
        break;
      case 3:
        t = 0.03;
        break;
      case 4:
        t = 0.04;
        break;
      case 5:
        t = 0.05;
        break;
    }
    if (selectedRarity > 0) {
      temp = mocktraits.filter(trait => trait.rarity > t);
    }
    setTraits(temp);
    setSelectedTrait(0);
  }, [selectedRarity]);

  return (
    <div className={styles.container}>
      <div className={styles.header}>
        <div className={styles.title}>ARMORY CHART</div>
        <div className={styles.description}>
          Purchase or sire with Minions with newly discovered traits.
        </div>
      </div>
      <div className={styles.categoryContainer}>
        <div className={styles.categoryList}>
          <DecorButton
            title="ALL"
            onClick={() => setSelectedAttribute(0)}
            active={selectedAttribute == 0}
          />
          <DecorButton
            title="NO ELEMENT"
            onClick={() => setSelectedAttribute(1)}
            active={selectedAttribute == 1}
          />
          <DecorButton
            title="FIRE"
            onClick={() => setSelectedAttribute(2)}
            active={selectedAttribute == 2}
          />
          <DecorButton
            title="WATER"
            onClick={() => setSelectedAttribute(3)}
            active={selectedAttribute == 3}
          />
          <DecorButton
            title="FOREST"
            onClick={() => setSelectedAttribute(4)}
            active={selectedAttribute == 4}
          />
        </div>
      </div>
      <div className={styles.traitPanel}>
        <div className={styles.traitList}>
          <SelectBox />
          <TraitList
            items={rarities}
            active={selectedRarity}
            onClick={setSelectedRarity}
          />
        </div>
        <CharacterBoard
          prevCharacter={selectedTrait > 0 ? traits[selectedTrait - 1] : {}}
          character={traits[selectedTrait]}
          nextCharacter={
            selectedTrait < traits.length - 1 ? traits[selectedTrait + 1] : {}
          }
          traitIndex={selectedTrait}
          onClick={selectTrait}
        />
      </div>
      <div className={styles.footer}>
        <div className={styles.title}>THANKS FOR BEING WITH US</div>
        <div className={styles.description}>
          Join our communities and keep track of the latest news
        </div>
        <input
          placeholder="Enter your email address"
          className={styles.subscribe}
        />
        <div className={styles.social}>
          <img src="image/Facebook.png" />
          <img src="image/Twitter.png" />
          <img src="image/Yutube.png" />
          <img src="image/Discord.png" />
        </div>
        <div>Â© 2021 Mighty Minions</div>
        <img src="image/Footer Background.png" />
      </div>
    </div>
  );
}
